//  border-bottom: 1px solid $lightGray;
//}

//.posts > .post:last-child {
//  padding-bottom: 1em;
//  border-bottom: none;
//}

article {
	@include mobile {
		margin: 0 20px;
	}
}

.post {
	img {
		margin: 30px 0;
	}
	blockquote {
		margin: 1.8em 0.8em;
		border-left: 1px dotted $gray;
		padding: 0.1em 1em;
		color: $gray;
	}
	.eyebrow {
		font-size: 12px;
		line-height: 30px;
		color: $darkGray;
		padding-top: 3px;
		margin-bottom: 3px;
		border-top: 1px solid $medGray;
	}
	.comments {
		margin-top: 10px;
	}
	.small-link {
		font-size: 11px;
		color: $medGray;
		display: block;
		&:hover {
			color: $red;
		}
	}
	.large-link {
		font-size: 20px;
		color: $red;
		font-weight: 500;
	}
	.primary-link {
		color: #000;
		font-size: 13px;
		font-weight: 500;
	}
	.spacer {
		padding-right: 8px;
		margin-right: 8px;
		border-right: 1px solid $lightGray;
	}
}

.wrapper-footer {
	margin-top: 50px;
	border-top: 1px solid #ddd;
	border-bottom: 1px solid #ddd;
	background-color: $lightGray;
}

footer {
	padding: 20px 0;
	text-align: center;
}

/*********************/
/* NEWSLETTER SIGNUP */
/*********************/
#updates {
	margin-top: 40px;
	padding-top: 40px;
	border-top: 1px solid #777777;
}
.form-container {
	margin-left: 0;
	margin-right: 0;
	width: 680px;
	max-width: 640px !important;
}
.emailoctopus-form-wrapper,
.form-container {
	width: 640px;
}
